// @jsx jsx
import React from 'react'
import { Link as GatsbyLink, graphql } from 'gatsby'
import Layout from '../components/layout'

import { jsx, Card, Grid, Heading, Link, Text } from 'theme-ui'

const HomePage = props => {
  const posts = props.data.allMdx.nodes

  return (
    <Layout pageSEO={{ title: 'Home' }}>
      <Grid
        gap={6}
        sx={{
          gridTemplateColumns: 'repeat(auto-fill, minmax(320px, 1fr))',
          justifyContent: 'center',
          maxWidth: 'responsiveStop',
          margin: 'auto'
        }}
      >
        {posts.map(post => (
          <Link
            key={post.id}
            as={GatsbyLink}
            to={post.fields.slug}
            sx={{ textDecoration: 'none' }}
          >
            <Card>
              {/* <Label
              corner
              icon={post.frontmatter.categoryLinked.icon}
              color={post.frontmatter.categoryLinked.color}
            /> */}
              <Heading>{post.frontmatter.title || post.fields.slug}</Heading>
              <Text variant="default" sx={{ color: 'textMuted' }}>
                {post.frontmatter.date}
              </Text>
              <Text variant="default">
                {post.frontmatter.description || post.excerpt}
              </Text>
            </Card>
          </Link>
        ))}
      </Grid>
    </Layout>
  )
}

export default HomePage

export const pageQuery = graphql`
  query {
    allMdx(
      filter: { fields: { sourceInstanceName: { eq: "blog" } } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      nodes {
        id
        excerpt
        fields {
          slug
        }
        frontmatter {
          categoryLinked {
            icon
            color
          }
          date(formatString: "MMMM DD, YYYY")
          description
          title
        }
      }
    }
  }
`
